.services {
	padding-top: 65px;
	padding-bottom: 94px;
	.column {
		&-1 {
			.h2 {
				padding-left: 6px; }
			>p {
				padding-left: 6px;
				color: #2d2d2d;
				font-family: HelveticaNeue, sans-serif;
				line-height: 25px; } }
		&-2 {
			padding-left: 76px;
			.h2 {
				padding-left: 4px;
				margin-bottom: 45px;
				white-space: nowrap; }
			p {
				padding-left: 6px;
				line-height: 25px;
				&:first-of-type {
					margin-bottom: 40px;
					font-size: 20px;
					text-decoration: underline;
					cursor: pointer; } }
			a {
				padding-left: 6px;
				color: #2d2d2d;
				&:hover {
					color: #a1907e; } } } } }

.address {
	margin-top: 40px;
	margin-bottom: 10px;
	.h3 {
		margin-bottom: 15px;
		font-size: 22px;
		color: #120400; }
	p {
		line-height: 25px;
		&:first-of-type {
			margin-bottom: 5px; }
		&:last-of-type {
			margin-top: 15px;
			text-decoration: underline; } }
	a {
		color: #120400;
		&:hover {
			color: #a1907e; } } }

.popup {
	&_body {
		overflow: hidden;
		padding-right: 17px; }
	&_overlay {
		display: none;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: rgba(0, 0, 0, 0.63);
		z-index: 98;
		overflow-y: scroll;
		&.active {
			display: block; } }
	&_hidden {
		position: fixed;
		top: 0;
		left: 0;
		right: 17px;
		bottom: 0;
		z-index: 99;
		background-color: transparent; }
	&_contact {
		position: relative;
		width: 572px;
		margin: 40px auto;
		padding: 57px 20px 69px;
		background-color: #f2f2f2;
		box-sizing: border-box;
		z-index: 100;
		&-btn {
			display: none;
			position: absolute;
			top: 0;
			left: 0;
			width: 45px;
			height: 45px;
			padding: 0 15px 15px 0;
			line-height: 45px;
			text-align: center;
			font-size: 25px;
			color: #a1907e;
			border-radius: 0 0 100% 0;
			cursor: pointer;
			opacity: 0.5;
			z-index: 101;
			transition: background-color 0.7s ease;
			&:hover {
				opacity: 1;
				color: $accent;
				background-color: #a1907e; }
			.fa {
				font-size: 45px; } }
		&-info {
			margin-bottom: 33px;
			padding: 0 50px;
			color: #2d2d2d;
			text-align: center;
			.h2 {
				margin-bottom: 43px;
				font-size: 35px;
				font-weight: 700;
				text-transform: uppercase; }
			p {
				&:first-of-type {
					margin-bottom: 45px; }
				&:last-of-type {
					margin-top: 12px; } }
			a {
				color: #2d2d2d;
				font-size: 25px;
				&:hover {
					color: #a1907e; } } } } }

.form {
	&-data {
		margin: 0 auto;
		max-width: 306px;
		.input, .select, .option, .textarea {
			background-color: #f2f2f2; }
		.select, .option {
			text-transform: uppercase; }
		&-group {
			position: relative;
			margin-bottom: 4px;
			.label {
				position: absolute;
				bottom: 11px;
				left: 4px;
				color: #939393;
				font-size: 16px;
				text-transform: uppercase;
				pointer-events: none;
				transition: transform 0.6s ease; }
			.input {
				padding-top: 30px;
				color: #3d3d3d;
				&:focus {
					& + .label {
						transform: translateY(-28px);
						font-size: 14px;
						text-transform: none; } }
				&:valid {
					& + .label {
						transform: translateY(-28px);
						font-size: 14px;
						text-transform: none; } } } }
		&-select {
			position: relative;
			padding-top: 15px;
			margin-bottom: 30px;
			.select {
				appearance: none;
				cursor: pointer;
				color: #939393; }
			.fa {
				position: absolute;
				right: 4px;
				bottom: 10px;
				font-size: 20px;
				pointer-events: none; }
			.option {
				color: #939393; } }
		&-textarea {
			margin-bottom: 30px;
			.label {
				padding-left: 4px;
				color: #505050;
				cursor: pointer; }
			.textarea {
				max-width: 306px;
				height: 181px;
				margin-top: 12px;
				font-family: $default-font;
				color: #3d3d3d;
				border: 1px solid #c9c9c9;
				box-sizing: border-box;
				resize: none; } }
		.btn {
			margin: 0 auto; } } }



