.banner-3 {
	padding: 140px 0 186px;
	text-align: center;
	color: #fff;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	.h1 {
		font-weight: 700; } }

.bonus {
	padding-top: 100px;
	padding-bottom: 130px;
	p {
		margin-bottom: 90px;
		text-align: center;
		color: #2d2d2d;
		font-family: HelveticaNeue, sans-serif;
		line-height: 25px; } }

.column {
	display: flex;
	box-sizing: border-box;
	.h2 {
		margin-bottom: 55px;
		color: #2d2d2d; }
	&-1 {
		width: 50%;
		padding-right: 140px;
		padding-top: 5px;
		box-sizing: border-box;
		ol {
			font-family: HelveticaNeue, sans-serif;
			line-height: 25px;
			list-style-type: decimal;
			list-style-position: inside; } }
	&-2 {
		width: 50%;
		padding-left: 83px;
		padding-top: 5px;
		box-sizing: border-box;
		border-left: 1px solid #a1907e; } }

.form {
	&-subscribe {
		&-group {
			margin-bottom: 46px;
			.input {
				max-width: 306px;
				&-email {
					padding-bottom: 8px; }
				&-text {
					padding-bottom: 11px;
					text-transform: uppercase; } }
			&:last-of-type {
				margin-bottom: 53px; } }
		&-leg {
			margin-bottom: 25px;
			padding-left: 4px;
			color: #939393 {
	 		font-size: 16px; }
			text-transform: uppercase; }
		&-checkbox {
			display: inline-block;
			margin-right: 25px;
			white-space: nowrap;
			color: #2d2d2d;
			user-select: none;
			&:last-child {
				margin-right: 0; }
			.checkbox {
				display: none;
				&:checked + label::before {
					background-position: 1px -1px; } }
			.label {
				cursor: pointer;
				&::before {
					content: '';
					display: inline-block;
					height: 25px;
					width: 25px;
					margin: 0 10px 0 4px;
					vertical-align: middle;
					background: -27px -1px url("../img/checkbox-sprite.png") no-repeat; } } } } }
